import React from 'react';
import Warper from './Warper';
import Popup from "reactjs-popup";

const contentStyle = {
  maxWidth: '1200px',
  width: '90%',
};

// <li>
                  //   We only care about the transcribed parts of the video. If the video is longer that 5 minutes, you should do your tasks based on the shown transcript and corresponding video content.
                  // </li>

const CustomModal = () => (
  <Popup
    trigger={<button type="button" className="button">View Instructions</button> }
    modal
    lockScroll={false}
    contentStyle={contentStyle}>
    {close => (
      <div className="modal">
        <a className="close" onClick={close}>
          &times;
        </a>
        <div className="content">
          <div className="col-12" id="actualInstructions">
            <h2>Tasks</h2>
            <dl>
              <dt>Select the BEST and WORST summaries according to three criteria: informativeness, fluency, and overall quality.</dt>              
              <p>👉 <em>Read</em> the whole transcript and four different summaries, and <em>select</em> the BEST and WORST summaries you think.</p>
              <dd>
                <ol>
                  <li>You must select <em>at least ONE</em> summary for each category.</li>
                    <ul>
                      <li>Informativeness: how much useful information about the video clip does the summary provide?</li>
                      <li>Fluency: the summary sentences should be grammatically correct, easy to read and understand</li>                      
                      <li>Overall quality: the entire summary quality including the above criteria</li>
                    </ul>
                  <li>All summaries are based on the given transcript and the corresponding video content (5 minute long).</li>
                  
                </ol>
              </dd>

            </dl>

            <h2>Notes</h2>
              <ol>                  
                  <li>You should use the video to understand the context of the transcript.</li>
                  <li>The title of the video is shown on the top of the transcript. </li>
              </ol>            

            <h2>Video Player Control</h2>
              <ul>
                  <li>
                      By click a timestamp next to each utterance, you can jump to that exact time in the video.
                  </li>
                  <li>
                    Clicking the video area enables play or pause the video.
                  </li>
              </ul>
          </div>
        </div>
      </div>
    )}
  </Popup>
);

export default Warper(CustomModal);
